<template>
    <div class="career" >
        <div class="wrap" :style="index===0?'border-top: none':''"  @click="showDetail">
            <div class="job">
                <strong class="title"><span v-if="job.first">[优先]</span> {{job.jobname}}</strong>
                <div class="area">
                    <span>{{job.department}}</span>
                    <span>{{job.area}}</span>
                    <span>全职</span>
                </div>
            </div>
            <div class="time" v-if="showtime">发布时间：{{job.time}}</div>
        </div>
        <transition name="fade">
        <div class="detail" v-show="showdetail">
            <h4>职位描述</h4>
            <p>{{job.desc}}</p>
            <h4>工作职责</h4>
            <ul>
                <li v-for="item in job.duty">{{item}}</li>
            </ul>
            <h4>职位要求</h4>
            <ul>
                <li v-for="item in job.requires">{{item}}</li>
            </ul>
            <h4>加分项</h4>
            <ul>
                <li v-for="item in job.first">{{item}}</li>
            </ul>
            <div class="btn"><a href="mailto:yanyi@mindeye.ai">发送简历</a></div>
        </div>
        </transition>
    </div>
</template>
<script>
import router from '../main.js'
export default {
    props: ['job','index'],
    data () {
        return {
            showtime: true,
            showdetail: false
        }
    },
    beforeMount () {
        if(window.innerWidth <= 680){
            this.showtime=false
        }
    },
    methods: {
        showDetail (e) {
            // router.push({ name: 'detail', params: { userId: 123, name: 'else' }})
            this.showdetail = !this.showdetail
        }
    }
}
</script>
<style scoped>
.career {
    padding-left: 36px;
    padding-right: 36px;
    display: flex;
    width: 80%;
    max-width: 820px;
    flex-direction: column;
    
    justify-content: space-between;
    margin: 0 auto
}
.career:hover {
    background-color: #f8f8f8;
}
.title {
    display: block;
    text-align: left;
    font-size: 20px;
    font-weight: normal;
    color: #5c6170;
}
.title span {
    color: #ff807a
}
.area {
    font-size: 16px;
    display: flex;
}
.area span {
    padding-right: 10px;
    color: #71727d
}
.area span + span {
    padding-left: 10px;
    border-left: solid 1px #e4e4d6
}
.time {
    color: #9a9fac
}
.wrap {
    height: 50px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 18px 0 18px 0;
    border-top: solid 1px #dddfe3;
    cursor: pointer;
}

.job {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.detail{
    text-align: left;
}
.btn{
    width: 100px;
    height: 40px;
    background-color: #1976d2;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px 0;
    border-radius: 5px;
}
.btn a{
    color: #fff;
    text-decoration: none;
    width: 100%;
    text-align: center;
}
@media screen and (max-width: 680px) {
    .title {
        font-size: 16px;
    }
    .area {
        font-size: 14px
    }
}
</style>



