<template>
	<section>
		<div v-if="direction=='left'||alignLeft"><img :src="url" :alt="url"></div>
		<div class="detail">
			<slot></slot>
			<a href="#">了解详细业务 ></a>
		</div>
		<div v-if="direction=='right'&&!alignLeft"><img :src="url" :alt="url"></div>
	</section>
</template>
<script>
  export default {
    props: ['direction', 'url'],
    data () {
      return {
        alignLeft: false
      }
    },
    mounted () {
      if (window.innerWidth < 800) {
        this.alignLeft = true
      }
      window.addEventListener('resize', () => {
        if (window.innerWidth < 800) {
          this.alignLeft = true
        } else if (window.innerWidth > 800) {
          this.alignLeft = false
        }
      })
    }
  }
</script>
<style scoped>
    section{
  	max-width: 1236px;
		display: flex;
		justify-content: space-between;
		margin: 0 auto;
	}
	section>div{
		width: 48%;
		display: flex;
		justify-content: center;
		flex-direction: column;
		box-sizing: border-box;
	}
	.detail{
		padding-left: 8%;
	}
	img{
		width: 100%;
		display: block;
	}
	h3,p,a{
		width: 68%;
		text-align: left;
		margin: 0;
		padding: 0;
		box-sizing: border-box;
	}
	p{
		line-height: 30px;
		font-size: 18px;
		margin-bottom: 20px
	}
	h3{
		margin: 0 0 20px 0;
		padding: 18px;
		text-align: center;
		border: solid 1px #333;
		font-size: 26px;
		line-height: 40px;
		font-weight: normal;
	}
	a{
		font-size: 18px;
		color: #00a1ea;
		text-decoration: none;
	}
	@media screen and (max-width: 1236px){
		section{
			justify-content: space-around;
		}
	}
	@media screen and (max-width: 1080px) {
		h3{
			font-size: 20px;
			line-height: 30px;
			padding: 12px;
		}
		p{
			line-height: 26px;
			font-size: 14px;
			margin-bottom: 14px
		}
	}

	@media screen and (max-width: 800px){
		section{
			flex-direction: column;
		}
		section>div{
			width: 100%;
		}
		.detail{
			padding: 20px 0 20px 0;
		}
		img{
			width: 100%;
			display: block;
			position: relative;
		}
		h3,p,a{
			margin: 5px auto;
			width: 80%;
			justify-content: center;
		}
		h3{
			font-size: 16px;
			line-height: 20px;
			padding: 6px;
		}
		a{
			font-size: 14px;
		}
		
	}
</style>



