<template>
    <section>
        <Job v-for="(item,index) in campus" :job="item" :index="index"></Job>
        <div></div>
    </section>
</template>
<script>
import Job from './Job'
import jobs from '../assets/jobs.js'
export default {
    components: { Job },
    data () {
        return {
            campus: jobs.campus
        }
    }
}
</script>
<style scoped>
section{
    margin-top: 30px;
    width: 100%
}
</style>



