<template>
  <div class="banner">
  	<h1>AI驱动世界</h1>
  </div>
</template>

<script>
import wave from './wave.js'
export default {
  data () {
    return {

    }
  },
  mounted () {
    wave(document.querySelector('.banner'))
  }
}
</script>

<style scoped>
@media (min-width: 1366px) {
	
}
.banner{
	width: 100%;
	background-color: #000;
	position: relative;
	z-index: 0;
	overflow: hidden;
}
h1{
	position: absolute;
	top: 30%;
	left: 0;
	right: 0;
	color: #ddd;
	letter-spacing: 4px;
}
@media screen and (min-width: 800px) {
	h1 {
		font-size: 48px;
	}
}
@media screen and (min-width: 1080px) {
	h1 {
		font-size: 60px;
		top: 20%;
	}
}
</style>



