<template>
    <div id="app" :style="top">
        <Navigator @gotarget="getCnt" @gojobs="gojobs" :slider="slider"></Navigator>
        <router-view @gojobs="gojobs"></router-view>
    </div>
</template>
<script>
import Navigator from './components/Navigator'
import router from './main.js'
import Jobs from './components/Jobs'
import Home from './components/Home'
export default {
    name: 'app',
    components: {
        Home,
        Navigator,
        Jobs
    },
    data() {
        return {
            height: 0,
            width: 0,
            cnt: 0, // 滚屏量
            slider: true, // 是否滚屏
            fromjob: false // 去除多余过渡效果
        }
    },
    methods: {
        getCnt(n) {
            if(this.slider===false){
                this.fromjob = true
                this.slider = true
            }else{
                this.fromjob = false
            }
            if( n!=='nocnt' ){
                this.cnt = n
            }
        },
        gojobs() {
            this.slider = false
        }
    },
    computed: {
        top () {
            if(this.width > 800 && this.slider){
                document.body.style.overflow='hidden'
            }else{
                document.body.style.overflow='visible'
            }
            let top;
            if(this.fromjob){
                top = 'top: ' + (-this.cnt * this.height) + 'px'
            }else{
                top = 'transition: all 0.6s;top: ' + (-this.cnt * this.height) + 'px'
            }
            return this.slider? top : 'position:static'
        }
    },
    mounted() {
        this.width = window.innerWidth
        this.height = window.innerHeight
        window.addEventListener('resize', () => {
            this.width = window.innerWidth
            this.height = window.innerHeight
        })
        let flag = true
        window.addEventListener('DOMMouseScroll', mouseWheel.bind(this))
        window.addEventListener('mousewheel', mouseWheel.bind(this))

        function mouseWheel(e) {
            let delta = e.wheelDelta || -e.detail
            this.fromjob = false
            if (flag) {
                flag = false
                setTimeout(() => {
                    flag = true
                }, 350)
                if (delta > 0 && this.cnt > 0) {
                    this.cnt--
                } else if (delta < 0 && this.cnt < 5) {
                    this.cnt++
                }
            }
        }
    }
}
</script>
<style>
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
}

body {
    margin: 0;
    font-family: "Helvetica", "Arial", sans-serif;
    height: 100%;
    overflow: hidden;
}

html {
    height: 100%;
}

@media screen and (max-width: 800px) {
    #app {
        position: static;
    }
    body {
        overflow: auto;
    }
}
</style>



