<template>
    <div class="navigator" @click="fHide" :style="backgroundcolor">
        <ul class="nav" :class="active?'show':'not-show'" v-if="phone">
            <li @click="gotarget('nocnt')"><a @click="goAnchor('#city')">智慧城市</a></li>
            <li @click="gotarget('nocnt')"><a @click="goAnchor('#drive')">自动驾驶</a></li>
            <li @click="gotarget('nocnt')"><a @click="goAnchor('#face')">面部识别</a></li>
            <li @click="gotarget('nocnt')"><a @click="goAnchor('#service')">服务网络</a></li>
            <li @click="gotarget('nocnt')"><a @click="goAnchor('#aboutUs')">关于我们</a></li>
            <li id="demo"  @click="gojobs"><a>招聘职位</a></li>
            <div style="flex-grow:1"></div>
        </ul>
        <ul class="nav" v-else>
            <li @click="gotarget(1)"><a>智慧城市</a></li>
            <li @click="gotarget(2)"><a>自动驾驶</a></li>
            <li @click="gotarget(3)"><a>面部识别</a></li>
            <li @click="gotarget(4)"><a>服务网络</a></li>
            <li @click="gotarget(5)"><a>关于我们</a></li>
            <li @click="gojobs" id="demo"><a>招聘职位</a></li>
            
        </ul>
        <div class="switch" @click.stop="fActive" :style="active?'background-color:#fff;transition:all 0.5s':'background-color:rgba(0,0,0,0);transition:all 0.5s'" :class="showBackground&&!active&&'showBackground'">
    		<span :class="active&&'firstActive'"></span>
    		<span :class="active&&'secondActive'"></span>
    		<span :class="active&&'thirdActive'"></span>
    	</div>
    </div>
</template>
<script>
import router from '../main.js'
export default {
  props: ['slider'],
  data () {
    return {
      active: false,
      width: 0,
      showBackground: false,
      backgroundcolor: '',
      flag: false
    }
  },
  methods: {
    fActive: function () {
      this.active = !this.active
    },
    fHide: function () {
      this.active = false
    },
    gotarget (n) {
      router.push('/')
      this.backgroundcolor = "background-color:#000;transition:all 0.3s"
      this.$emit('gotarget', n)
    },
    gojobs () {
      this.$emit('gojobs')
      router.push('/jobs')
      if(window.scrollY < 80){
      	this.backgroundcolor = "background-color:rgba(0,0,0,0);transition:all 0.3s"
      }
    },
    goAnchor (id) {
      // 上个页面注销，本页面挂在后再跳转
      setTimeout(()=>{
        var anchor = document.querySelector(id)
        document.body.scrollTop = anchor.offsetTop
      },100)
    }
  },
  mounted () {
    window.addEventListener('scroll', ()=>{
      if (window.scrollY > 400) {
        this.showBackground = true
      } else {
        this.showBackground = false
      }
      if(window.scrollY > 80){
      	this.backgroundcolor = "background-color:#000;transition:all 0.3s"
      }else if(window.scrollY < 80 && (this.phone || !this.slider)){
      	this.backgroundcolor = "background-color:rgba(0,0,0,0);transition:all 0.3s"
      }
    })
    this.width = window.innerWidth
    window.addEventListener('resize', () => {
      this.width = window.innerWidth
    })
  },
  computed: {
    phone: function(){
      return this.width < 800
    }
  }
}
</script>
<style scoped>
/*默认宽度大于800*/
.navigator {
    width: 100%;
    height: 86px;
    background-color: #000;
    z-index: 10;
	position: fixed;
	top: 0;
	left: 0
}
ul{
    margin: 0;
    padding: 0;
    width: 100%;
	list-style: none;
	box-sizing: border-box;
	font-size: 16px;
}
.nav {
	box-sizing: border-box;
	display: flex;
	height: 100%;
	justify-content: flex-end;
	align-items: center;
	padding-right: 45px;
	background: url(../assets/logotop.png) no-repeat 24px center;
    background-size: 150px;
}
.nav li {
	margin-left: 18px;
	font-weight: bold;
	cursor: pointer;
}
a{
	text-decoration: none;
	color: #fff;
}
#demo {
	border: solid 2px #fff;
	padding: 8px 15px;
	border-radius: 50px;
}
.switch {
	display: none;
	cursor: pointer;
}
/*汉堡动画*/
.firstActive {
	transition: all 0.5s;
	transform: translate(0px,10px) rotate(45deg);
	background-color: #888!important;
}
.secondActive {
	transition: all 0.5s;
	opacity: 0;
	background-color: #888!important;
}
.thirdActive {
	transition: all 0.5s;
	transform: translate(0px,-10px) rotate(-45deg);
	background-color: #888!important;
}

/*屏幕小于800时*/
@media screen and (max-width: 800px) {
	.navigator {
		height: 48px;
		background: url(../assets/logotop.png) no-repeat 98% 5px #000;
		background-size: 100px;
		position: absolute;
	}
	.nav {
		display: block;
		transition: height 0.2s, padding 0.3s;
		position: fixed;
		padding: 0 24px 0 24px;
		background: #fff;
		height: 0px;
		overflow: hidden;
		left: 0;
		top: 0;
	}
	.nav a{
		color: #888;
		display: block;
		width: 100%;
		height: 100%;
	}
	.nav li {
		height: 46px;
		line-height: 46px;
		margin: 0;
		padding: 0 0 0 30px;
		border-top: solid 1px #eee;
		text-align: left;
	}

	#demo {
		height: 86px;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		border-top: 1px solid #eee;
		border-radius: 0;
	}
	#demo a {
		height: 46px;
		background-color: #2eb6dc;
		width: 100%;
		border-radius: 40px;
		text-align: center;
		color: #fff;
	}
	.show {
		height: 396px;
		padding: 74px 24px 0 24px;
	}
	.switch {
		display: block;
		position: fixed;
		width: 40px;
		height: 40px;
		background-color: #888888;
		left: 10px;
		top: 10px;
		border-radius: 5px;
	}
	.switch span {
		display: block;
		height: 2px;
		margin: 8px 6px;
		background-color: #ccc;
		transition: all 0.5s;
	}
	.showBackground{
		background-color: #444!important
	}
}
/*屏幕宽度大于1366时,定宽为1366居中*/
@media screen and (min-width: 1366px) {
	.nav{
		width: 1366px;
		margin: 0 auto;
	}
}
</style>



