<template>
	<div class="foot">
		<img class="logo" src="../assets/logowhite.png">
		<p class="desc" v-if="!limit">“MindSee智见”最初由汽车行业资深人士为主要成员组成的创始团队，<br>现在已经形成一套以算法、产品、硬件、设计、运营在内的完整体系，<br>团队成员来自于国内外知名院校的毕业生，以及来自知名企业与研究机构。</p>
		<p class="desc" v-if="limit">“MindSee智见”最初由汽车行业资深人士为主要成员组成的创始团队,现在已经形成一套以算法、产品、硬件、设计、运营在内的完整体系，团队成员来自于国内外知名院校的毕业生，以及来自知名企业与研究机构。</p>
		<div class="wish"><span>我们致力于通过图像识别、机器学习和人工智能技术，为智慧城市交通，驾驶辅助乃至自动驾驶做出更大的贡献。</span></div>
		<div class="contact">
			<div class="phone"><img src="../assets/icon_phone.png"><span>02782285513</span></div>
			<div class="email"><img src="../assets/icon_mail.png"><span>yanyi@mindeye.ai</span></div>
			<div class="location"><img src="../assets/icon_location.png"><span>武汉东湖高新区光谷金融港A3栋4F</span></div>
		</div>
	</div>
</template>
<script>
  export default {
    data () {
      return {
        limit: false
      }
    },
    mounted () {
      if (window.innerWidth < 800) {
        this.limit = true
      } else {
        this.limit = false
      }
      window.addEventListener('resize', () => {
        if (window.innerWidth < 800) {
          this.limit = true
        } else {
          this.limit = false
        }
      })
    }
  }
</script>
<style scoped>
    .foot{
		color: #fff;
		width: 100%;
		height: 100%;
		background-color: #121a20;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		box-sizing: border-box;
		padding-top: 86px;
	}
	.logo{
		width: 16%;
	}
	.desc{
		margin: 0;
		font-size: 18px;
		line-height: 32px;
	}
	p{
		margin: 0;
	}
	.wish{
		width: 96%;
		height: 136px;
		border-top: solid 5px #d2d5d4;
		border-bottom: solid 5px #d2d5d4;
		background-color: #1f262b;
		font-size: 24px;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 20px 0 20px;
		box-sizing: border-box;
	}
	.phone,.location,.email{
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 20%;
	}
	.contact{
		display: flex;
		justify-content: center;
		width: 100%;
	}
	.contact img{
		margin-bottom: 38px;
	}
	@media screen and (max-width: 800px){
		.logo{
			margin: 20px;
		}
		.desc{
			text-align: left;
			padding: 20px;
			text-indent:2em;
			font-size: 14px;

		}
		.wish span {
			font-size: 16px;
			height: 60px;
		}
		.foot{
			padding-top: 0px;
		}
		.contact{
			flex-direction: column;
			justify-content: center;
			width: 100%;
			padding-left: 20px;
			margin: 30px;
		}
		.phone,.location,.email{
			align-items: center;
			flex-direction: row;
			width: 100%;
			font-size: 14px;
		}
		.contact img{
			width: 30px;
			margin: 5px;
		}
		.company{
			font-size: 12px;
			margin: 20px;
		}
	}
</style>



