import THREE from '../assets/three.js'
export default function( three ) {
    var SEPARATION = 100
    var AMOUNTX = 20
    var AMOUNTY = 20
    var camera, scene, renderer
    var particles, particle, count = 0
    var mouseX = 0,
        mouseY = -280
    var windowHalfX = window.innerWidth / 2
    var windowHalfY = window.innerHeight / 2
    init()
    animate()

    function init() {
        if(window.innerWidth >= 800) {
            camera = new THREE.PerspectiveCamera(75, window.innerWidth/window.innerHeight, 1, 10000)
        } else if (window.innerWidth < 800) {
            camera = new THREE.PerspectiveCamera(75, window.innerWidth/window.innerWidth, 1, 10000)
        }
        
        camera.position.z = 1000
        camera.position.y = 1000
        camera.position.x = 1000
        scene = new THREE.Scene()
        particles = new Array()
        var PI2 = Math.PI * 2
        var material = new THREE.ParticleCanvasMaterial({
            color: 0x209fd3,
            program: function(context) {
                context.beginPath()
                context.arc(0, 0, 1, 0, PI2, true)
                context.fill()
            }
        })
        var i = 0
        for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
                particle = particles[i++] = new THREE.Particle(material)
                particle.position.x = ix * SEPARATION - ((AMOUNTX * SEPARATION) / 2)
                particle.position.z = iy * SEPARATION - ((AMOUNTY * SEPARATION) / 2)
                scene.add(particle)
            }
        }
        renderer = new THREE.CanvasRenderer()
        if(window.innerWidth >= 800) {
            renderer.setSize(window.innerWidth, window.innerHeight)
        } else if (window.innerWidth < 800) {
            renderer.setSize(window.innerWidth, window.innerWidth)
        }
        three.appendChild(renderer.domElement)
        three.addEventListener('mousemove', onDocumentMouseMove, false)
        window.addEventListener('resize', onWindowResize, false)
        return renderer.domElement
    }

    function onWindowResize() {
        windowHalfX = window.innerWidth / 2
        windowHalfY = window.innerHeight / 2
        camera.aspect = window.innerWidth / window.innerHeight
        camera.updateProjectionMatrix()
        if(window.innerWidth >= 800) {
            renderer.setSize(window.innerWidth, window.innerHeight)
        } else if (window.innerWidth < 800) {
            renderer.setSize(window.innerWidth, window.innerWidth)
        }
    }

    function onDocumentMouseMove(event) {
        mouseX = event.clientX - windowHalfX
    }

    function onDocumentTouchStart(event) {
        if (event.touches.length === 1) {
            event.preventDefault()
            mouseX = event.touches[0].pageX - windowHalfX
        }
    }

    function onDocumentTouchMove(event) {
        if (event.touches.length === 1) {
            event.preventDefault()
            mouseX = event.touches[0].pageX - windowHalfX
        }
    }

    function animate() {
        requestAnimationFrame(animate)
        render()
    }

    function render() {
        camera.position.x += (mouseX - camera.position.x) * .05
        camera.position.y += (-mouseY - camera.position.y) * .05
        scene.position.set(0, 150, 0)
        camera.lookAt(scene.position)
        var i = 0
        for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
                particle = particles[i++]
                particle.position.y = (Math.sin((ix + count) * 0.3) * 50) + (Math.sin((iy + count) * 0.5) * 50)
                particle.scale.x = particle.scale.y = (Math.sin((ix + count) * 0.3) + 1) * 3 + (Math.sin((iy + count) * 0.5) + 1) * 3
            }
        }
        renderer.render(scene, camera)
        count += 0.1
    }
}



