<template>
    <div>
        <header>
            <div class="slogn">
                <img src="../assets/mindsee.png">
                <span>Fun on the Road</span>
                <h2>-AI驱动世界-</h2>
            </div>
            <img class="banner" src="../assets/job_banner.jpg">
        </header>
        <section>
            <div class="content">
            	<div class="top">
            		<h3>加入智见，见证人工智能的未来</h3>
            		<h3>我们希望你是个有梦的人，去相信，去证明。</h3>
            	</div>
            	<div class="bottom">
            		<div class="item" @click="goSocial">
            			<img src="../assets/social.png" alt="社会招聘">
            			<h4>社会招聘</h4>
            			<h5>Hot Jobs</h5>
            		</div>
            		<div class="item" @click="goCampus">
            			<img src="../assets/student.png" alt="校园招聘">
            			<h4>校园招聘</h4>
            			<h5>Campus Recruiting</h5>
            		</div>
            	</div>
            	<router-view></router-view>	
            </div>
        </section>
    </div>
</template>
<script>
import router from '../main.js'
export default {
	mounted () {
		this.$emit('gojobs')
	},
	methods: {
		goCampus () {
			router.push('/jobs/campus')
		},
		goSocial () {
			router.push('/jobs/social')
		}
 	}
}
</script>
<style scoped>
header {
    position: relative;
}
.top h3{
	font-size: 26px;
	font-weight: normal;
	color: #3d3333;
	text-align: center;
}
.top h4{
	font-size: 26px;
	margin: 10px 0;
	font-weight: normal;
}

.top span{
	font-size: 20px;
	color: #ccc;
	font-weight: normal;
	text-align: center;
}

.banner {
    width: 100%
}

.slogn {
    position: absolute;
    top: 45%;
    color: #fff;
    left: 0;
    right: 0;
}

header h2 {
    letter-spacing: 20px;
}

header span {
    font-size: 30px;
    margin-left: 20px;
}
.content{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.bottom{
	display: flex;
	flex-wrap: wrap;
	width: 46%;
	box-sizing: border-box;
	padding: 0 3% 0 3%;
	justify-content: space-between;
}
.item{
	width: 46%;
	border-radius: 5px;
	box-shadow: 0 0 5px #333;
	margin: 2%;
	display: flex;
	flex-direction: column;
	align-items: center;
	cursor: pointer;
}
.item img{
	width: 32%;
	margin: 30px auto;
}
.bottom h4{
	font-size: 28px;
	width: 84%;
	border-top: solid 2px #d0d0d0;
	margin: 0;
	padding-top: 18px;
	font-weight: 500;
}
.bottom h5{
	color: #d0d0d0
}
h1{
	font-weight: normal;
	font-size: 30px;
	letter-spacing: 5px;
	margin: 50px auto; 
}
.top{
	text-align: left;
}
section{
	margin-bottom: 150px;
}
@media screen and (max-width: 800px){
	.content{
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
	}
	.bottom{
		width: 100%
	}
	h1{
		font-size: 26px;
		margin: 30px auto; 
	}
	.top h3{
		font-size: 20px;
	}
	.top h4{
		font-size: 20px;
		margin: 10px 0;
		font-weight: normal;
	}

	.top span{
		font-size: 16px;
		color: #ccc;
		font-weight: normal;
	}
	.top{
		text-align: center;
		margin-bottom: 50px;
	}
	header h2 {
	    letter-spacing: 10px;
	    font-size: 18px;
	}
	.slogn img{
		width: 100px
	}
	header span {
	    font-size: 18px;
	    margin-left: 20px;
	}
	.banner {
 	   width: 100%;
 	   height: 200px;
	}
	.bottom h4{
		font-size: 18px;
	}
}
</style>



