<template>
    <div>
        <section class="slider" :style="sliderHeight">
            <Banner></Banner>
        </section>
        <section class="slider" :style="sliderHeight" id="city">
            <TitleBar title="智慧城市" :url="require('../assets/city_traffic.png')" v-show="showTitle"></TitleBar>
            <Item direction="left" :url="require('../assets/city.jpg')">
                <h3>DCC EYE 智慧城市交通辅助系统</h3>
                <p>为城市交通提供基于移动视频的城市管理辅助系统，可有效补充城市固定监控无法覆盖的区域的交通实时状况监测。</p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="drive">
            <TitleBar title="自动驾驶" :url="require('../assets/drive_assistance.png')" v-show="showTitle"></TitleBar>
            <Item direction="right" :url="require('../assets/drive.jpg')">
                <h3>ADAS辅助驾驶系统</h3>
                <p>为汽车提供高精确度的驾驶安全算法，为前装市场、后装市场设计专门的算法模型，为车载智能硬件提供ADAS SDK，在各种硬件平台上实现精确的ADAS功能。</p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="face">
            <TitleBar title="面部识别" :url="require('../assets/micro_expression.png')" v-show="showTitle"></TitleBar>
            <Item direction="left" :url="require('../assets/face.jpg')">
                <h3>FACE CODE 面部微表情识别系统</h3>
                <p>基于深度学习的卷积神经网络为疲劳检测及面部表情分析提供基于心理分析的面部表情识别系统，在微表情识别率上达到目前该领域的最高识别效果。</p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="service">
            <TitleBar title="服务网络" :url="require('../assets/car_service.png')" v-show="showTitle"></TitleBar>
            <Item direction="right" :url="require('../assets/map.jpg')">
                <h3>汽车后市场服务网络</h3>
                <p>遍布湖北、江西、重庆、四川近千家汽车后服务网点，为银联、农行、华夏银行等金融机构、人保、平安等保险机构提供后市场汽车服务。</p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="aboutUs">
            <Foot></Foot>
        </section>
    </div>
</template>
<script>
import Banner from './Banner'
import Item from './Item'
import TitleBar from './TitleBar'
import Foot from './Foot'
export default {
    name: 'app',
    components: {
        Banner,
        Item,
        TitleBar,
        Foot
    },
    data() {
        return {
            height: 0,
            width: 0,
            cnt: 0,
            slider: true
        }
    },
    methods: {

    },
    computed: {
        sliderHeight: function() {
            return this.width > 800 ? 'height:' + this.height + 'px' : 'height:' + 'auto'
        },
        showTitle: function() {
            return this.width > 800
        }
    },
    mounted() {
        this.width = window.innerWidth
        this.height = window.innerHeight
        window.addEventListener('resize', () => {
            this.width = window.innerWidth
            this.height = window.innerHeight
        })
    }
}
</script>
<style>
.slider {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
</style>



